  z-index: 10;
  position: relative;
}

video#video_background {
  position: absolute;
  left: 0;
  top: 0;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  display: block;
}

.video-block .pattern {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: url(${___CSS_LOADER_URL_REPLACEMENT_29___});
  z-index: 1;
}

@media only screen and (min-width: 768px) {
  .top-slider .item {
    height: 400px;
  }
  .top-slider {
    height: 400px;
  }
  .header-push {
    height: 100px;
    transition: all 200ms ease-out;
  }
  .header-wrapper {
    height: 100px;
    transition: all 200ms ease-out;
  }
  .header-wrapper.hPanelHide {
    top: -36px;
  }
}
@media only screen and (min-width: 992px) {
  .top-slider .item {
    height: 520px;
  }
  .top-slider {
    height: 520px;
  }
  .top-slider .table {
    opacity: 0;
    visibility: hidden;
    transform: scale(1.5);
    transition: 0.5s;
  }
  .top-slider .table.active {
    webkit-transform: scale(1);
    transform: scale(1);
    opacity: 1;
    visibility: visible;
  }
}
@media only screen and (min-width: 1240px) {
  .top-slider .text {
    font-size: 24px;
    line-height: 1.6;
  }
}
@media only screen and (min-width: 1360px) {
  .top-slider .item {
    height: 600px;
  }
  .top-slider {
    height: 600px;
  }
}
/* end top slider */
/************************* accordeon **************************/
.accordeon .element-content {
  display: none;
  padding: 20px;
  font-size: 13px;
  color: #888885;
}

.accordeon .element {
  margin-bottom: 6px;
  background: #fff;
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.1);
}

.accordeon .element .title {
  cursor: pointer;
  padding: 17px 15px 17px 70px;
  text-transform: uppercase;
  font-family: "Helios";
  font-weight: bold;
  font-size: 13px;
  letter-spacing: 0.5px;
  position: relative;
  color: #888885;
  transition: all 200ms ease-out;
}

.accordeon .element.active .title {
  background: #e9bb26;
  color: #fff !important;
}

.accordeon .element .title:before {
  font-family: "FontAwesome";
  content: "";
  display: block;
  position: absolute;
  width: 50px;
  height: 100%;
  top: 0;
  left: 0;
  background: url(${___CSS_LOADER_URL_REPLACEMENT_30___}) 50% 50% no-repeat #ccc;
  color: #fff;
  text-align: center;
  line-height: 50px;
  transition: all 200ms ease-out;
}

.accordeon .element.active .title:before {
  background: url(${___CSS_LOADER_URL_REPLACEMENT_31___}) 50% 50% no-repeat #333;
}

.accordeon .element-content-active {
  display: block;
}

.accordeon .element .title:hover:before {
  background-color: #333;
}

.accordeon .element .title:hover {
  color: #333333;
}

.accordeon > div:last-of-type {
  margin-bottom: 0;
}

/************************* end accordeon **************************/
/**** steps section *****/
.steps-section .img-wrapper {
  background: url(${___CSS_LOADER_URL_REPLACEMENT_32___}) 50% 0 no-repeat #fff;
  width: 130px;
  height: 70px;
  text-align: center;
  line-height: 70px;
  font-weight: bold;
  margin: auto;
  color: #2364a6;
  padding: 0 30px;
  position: relative;
  z-index: 4;
}

.steps-section .text {
  font-size: 14px;
}

@media only screen and (min-width: 768px) {
  .steps-section .inner:before {
    content: "";
    display: block;
    position: absolute;
    width: 70%;
    height: 1px;
    top: 30px;
    left: 15%;
    background: #2364a6;
  }
}
/**** /. steps section *****/
/**** services section *****/
.services-arrow {
  display: flex;
  justify-content: center;
}

.arrow-right,
.arrow-left {
  width: 30px;
  height: 30px;
  cursor: pointer;
}

.arrow-left {
  transform: rotate(180deg);
  margin-right: 20px;
}

.arrow-right {
  margin-left: 20px;
}

.services-section .element {
  margin-bottom: 20px;
}

.services-section .element .element-content {
  padding: 20px;
  background: #fff;
  transition: all 200ms ease-out;
}

.services-section .element .img-wrapper {
  overflow: hidden;
  position: relative;
  background: #fff;
  padding: 15px 15px 0;
}

.services-section .element .img-wrapper:after {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.6);
  opacity: 0;
  transition: all 200ms ease-out;
}

.services-section .element:hover .img-wrapper:after {
  opacity: 1;
}

.services-section .element .img-wrapper:before {
  content: "Оформить заявку";
  display: block;
  position: absolute;
  width: 200px;
  height: 48px;
  top: 50%;
  left: 50%;
  margin-left: -100px;
  transition: all 200ms ease-out;
  margin-top: -24px;
  z-index: 2;
  opacity: 0;
  color: #fff;
  padding: 10px 0;
  text-align: center;
  text-transform: uppercase;
  border: 2px solid #fff;
}

.services-section .element:hover .img-wrapper:before {
  opacity: 1;
}

.services-section .element .img-wrapper img {
  transition: all 200ms ease-out;
}

.services-section .element:hover .img-wrapper img {
  -webkit-transform: scale(1.2);
  -moz-transform: scale(1.2);
  -o-transform: scale(1.2);
}

.services-elements .title-h5 {
  overflow: hidden;
  position: relative;
  padding-right: 40px;
  color: #22282e;
  font-weight: bold;
  line-height: 1.2;
  margin-bottom: 10px;
  height: 40px;
}

.services-elements .title-h5:after {
  content: "";
  display: block;
  position: absolute;
  width: 16px;
  height: 16px;
  top: 3px;
  right: 3px;
  border-top: 2px solid #e9bb26;
  border-right: 2px solid #e9bb26;
  transition: all 200ms ease-out;
}

.services-section .element:hover .title-h5:after {
  transform: rotate(45deg);
}

.services-elements .text {
  color: #777777;
  font-size: 14px;
  height: 66px;
  overflow: hidden;
}

.services-elements img {
  width: 100%;
}

.services-elements > .row > hr:nth-of-type(2n) {
  clear: both;
}

.services-elements .element-price {
  margin-bottom: 15px;
  font-weight: bold;
  color: #2fab6d;
}

.up {
  position: fixed;
  bottom: 50px;
  right: 50px;
  width: 70px;
  height: 70px;
  z-index: 300;
  border: 4px solid #e9bb26;
  padding: 0 2px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.6);
}

@media only screen and (min-width: 992px) {
  .services-elements > .row > hr:nth-of-type(2n) {
    clear: none;
  }
  .services-elements > .row > hr:nth-of-type(3n) {
    clear: both;
  }
}
