
import svg from '../images/spinner.svg'

export const form = () => {
    const form = document.querySelector('form')
    const statusBlock = document.createElement('div')
    
    // statusBlock.insertAdjacentHTML('beforeend', `<img src=${svg} alt=${svg}`)

    const loadedMessage = svg
    const errorMessage = 'Ошибка загрузки данных ...'
    const successMessage = 'Спасибо! Наш менеджер скоро свяжется с вами!!!!'
    
    statusBlock.style.cssText = `
        color : black;
        text-align: center;
    `
        
    // statusImg.style.cssText = `
    //     color: yellow;
    //     display: block;
    //     margin: 0 auto;
    //     text-align:center;
// `;
    statusBlock.insertAdjacentHTML('beforeend', `<img src=${svg} alt=${svg}`)
    // statusBlock.textContent = "Загрузка..."
    form.append(statusBlock)

    const sendData = (data) => {
        return fetch('https://jsonplaceholder.typicode.com/posts', {
            method: 'POST',
            body: JSON.stringify(data),
            headers: {
                "Content-Type": "application/json"
            }
        }).then(res => res.json())
        
    }   

    const sendForm = (form) => {

        const listsInput = form.querySelectorAll('input')
        const formData = new FormData(form)
        const formBody = {}


        statusBlock.insertAdjacentHTML('beforeend', `<img src=${svg} alt=${svg}`)
        form.append(statusBlock)




        statusBlock.textContent = 
        formData.forEach((input, key) => {
            formBody[key] = input

        })
        sendData(formBody)
    }
    
    form.addEventListener('submit', (e) => {
        e.preventDefault()
        sendForm(form)

    })
}